import React from 'react';
import { Block, Text, Link } from '../elements';

const Component = () => (
  <Block>
    <Text textColor='blue'>Borders have guards and the guards have guns</Text>
    <Text><Link to='/e/2'>620 sheets of paper are rustling in the wind</Link></Text>
  </Block>
);

export default Component;



